body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
  margin: 2em 0 2em 2em;
  padding-bottom: 1em;
  border-bottom: 1px solid;
  display: flex;
  justify-content: space-between;
  width: 100%;
  align-items: center;

}

.header a {
  margin: 0 1em 0 1em;
  font-size: 1.2em;
  text-decoration: none;
}

.content {
  margin-left: 2em;
  font-size: 1.5em;
}

.message {
  color: red;
  font-size: 0.7em;

}

.Header-title{
  color: red;
}


.header-navbar{
  margin: 0px 100px 0px 0px;
}

.Header-title {
  margin: 0px 0px 0px 50px;
}

.content{
  width: 100%;
  display: flex;
  justify-content: center;
  margin: 50px 0px 0px 0px;
}

.form{
  width: 500px;
  height: 650px;
  border: solid 1px gray;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 25px;
  background-color: whitesmoke;
}

.forml {
  width: 500px;
  height: 500px;
  border: solid 1px gray;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 25px;
  background-color: whitesmoke;
}

.form-box{
  display: block;
  width: 100%;
  text-align: center;
}

.form-label-and-input{
  margin-bottom: 20px;
}

.form-center{
  height: 100%;
}

.form-title {
  text-align: center;
  font-size: 36px;
  font-weight: bolder;
  color: orange;
}

.form-label-and-input{
  width: 250px;
}

.form-input{
  width: 100%;
  height: 25px;
  font-weight: normal;
  font-size: 20px;
}

.form-input-Button{
  width: 100%;
  height: 50px;
  border: solid 2px white;
  background-color: green;
  color: white;
  font-size: 24px;
  font-weight: bold;
  cursor: pointer;
}

.form-input-Button:hover{
  background-color: white;
  color: green;
  border: solid 2px green;
}

.message-styles{
  width: 100%;
  text-align: center;
}